<template>
  <div class="box">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span class="hbirdText">
          <i class="el-icon-loading"></i> Hbird System
          <img src="../img/bird.jpg" alt style="width:40px;height:50px;" />
        </span>
      </div>
      <div>
        账号:
        <el-input class="input" v-model="userName" placeholder="请输入账号"></el-input>
      </div>
      <div>
        密码:
        <el-input class="input" v-model="password" type="password" placeholder="请输入密码"></el-input>
      </div>
      <div class="btnBox">
        <el-button type="success" round @click="goLogin">登录</el-button>
        <el-button type="primary" round @click="dialogVisible = true">注册</el-button>
      </div>
    </el-card>

    <!-- 注册 -->
    <el-dialog title="注册" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <div>
        <div>
          账号:
          <el-input class="input" v-model="registerUserName" placeholder="请输入账号"></el-input>
        </div>
        <div>
          密码:
          <el-input class="input" v-model="registerPassword" placeholder="请输入密码"></el-input>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="hiddenDialog">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Api from "../api/index.js";
export default {
  data() {
    return {
      userName: "",
      password: "",
      registerUserName: "",
      registerPassword: "",
      dialogVisible: false
    };
  },
  mounted() {
    localStorage.setItem("isRegister", false);
  },
  methods: {
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    goLogin() {
      if (this.userName === "" || this.password === "") {
        this.$message({
          message: "请输入账号密码",
          type: "error"
        });
        return;
      }

      this.$axios
        .post(Api.login, {
          name: this.userName,
          pwd: this.password
        })
        .then(res => {
          if (res.data.error === 1) {
            this.$message({
              message: res.data.message,
              type: "error"
            });
            this.userName = this.password = "";
          } else {
            localStorage.setItem("isRegister", true);
            localStorage.setItem("hbToken", res.data.data.token);
            this.$router.push({ name: "Home" });
            this.$message({
              message: "登录成功",
              type: "success"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    hiddenDialog() {
      if (this.registerUserName !== "" && this.registerPassword !== "") {
        this.$axios
          .post(Api.register, {
            name: this.registerUserName,
            pwd: this.registerPassword
          })
          .then(res => {
            console.log(res);
            if (res.data.error === 1) {
              this.$message({
                message: res.data.message,
                type: "error"
              });
              this.registerUserName = this.registerPassword = "";
            } else {
              this.$message({
                message: "注册成功",
                type: "success"
              });
              this.dialogVisible = false;
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.$message({
          message: "请输入注册账号密码",
          type: "error"
        });
      }
    }
  }
};
</script>


<style scoped>
.box {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  font-family: "Comic Sans MS", cursive, sans-serif;
  overflow: hidden;
  background: url("../img/hbird.gif") no-repeat;
  background-size: 100% 100%;
}
.input {
  width: 90%;
  margin-bottom: 5%;
}
.btnBox {
  float: right;
  margin-bottom: 5%;
}
.hbirdText {
  font-weight: bolder;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 480px;
  box-shadow: 0 0 15px #4e4e4e;
  padding: 10px;
  background-color: #f6f6f6;
}
</style>