<template>
  <div>
    <template v-for="(item,i) in list">
      <el-radio v-model="comData.content" :label="item" :key="i">{{item}}</el-radio>
    </template>
  </div>
</template>

<script>
export default {
  name: "hb-radio",
  props: {
    comData: Object,
    list: {
      type: Array,
      default: () => {
        return ["备选"];
      }
    }
  },
  data() {
    return {};
  }
};
</script>