<template>
  <div :style="{display:isBlock?'block':'inline-block'}">
    <el-switch
      v-model="comData.content"
      :style="{width:width+'px'}"
      :active-color="openColor"
      :inactive-color="closeColor"
      v-bind="comData"
    ></el-switch>
  </div>
</template>
<script>
export default {
  name: "hb-switch",
  props: {
    content: {
      type: Boolean,
      default: false,
    },
    width: {
      type: String,
      default: "100",
    },
    openColor: {
      type: String,
      default: "#409EFF",
    },
    closeColor: {
      type: String,
      default: "#C0CCDA",
    },
    isBlock: {
      type: Boolean,
      default: false,
    },
    comData: Object,
  },
};
</script>