import Vue from 'vue'
import App from './system/main.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import hbird from '@hbird/comps'
import store from './store'
import axios from 'axios'
import router from './router'
import hbFormItem from './components/form/hb_form_item.vue'
import hbGrid from './components/grid/hb_grid.vue'
Vue.use(ElementUI);
Vue.use(hbird)


Vue.config.productionTip = false
Vue.prototype.$axios = axios

axios.interceptors.request.use(res => {
  res.headers.common['token'] = localStorage.getItem('hbToken') || ''
  return res;
});

Vue.component('hbFormItem',hbFormItem)
Vue.component('hbGrid',hbGrid)




new Vue({
  router,
  store: store,
  render: h => h(App),
  data: {
    eventHub: new Vue()
  }
}).$mount('#app')
