<template>
  <el-form :label-width="labelwidth+'px'" :label-position="labelposition" :inline="JSON.parse(inline)">
    <slot></slot>
  </el-form>
</template>
<script>
export default {
  name: "hb-form",
  props: {
    labelwidth: {
      default: 100
    },
    labelposition: {
      default: "left"
    },
    inline: {
      default: "false"
    }
  },
  data() {
    return {};
  },
};
</script>
