<template>
  <hb-input :comData="comData" v-bind="comData" :rule="rule" :placeholder="placeholder"></hb-input>
</template>
<script>
export default {
  name: "hb-card",
  props: {
    comData: Object,
    placeholder: {
      //外部定义的可配置属性,这里添加默认值
      type: String,
      default: "请输入身份证"
    }
  },
  data() {
    return {
      rule: /^([1-6][1-9]|50)\d{4}(18|19|20)\d{2}((0[1-9])|10|11|12)(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/
    };
  },
  methods: {}
};
</script>
