<template>
  <div class="backbg">
    <div style="float:left">
      <img src="../img/bird.jpg" width="35px" alt style="float:left;" />
      <span style="float:left;" class="vintage4">Hbird System</span>
    </div>
    <!-- 头部 -->
    <div style="float:right;margin-top:5px;">
      <el-button round @click="navToPage">预览</el-button>
      <el-button type="warning" round @click="newPage">新页面</el-button>
      <el-button type="info" round @click="drawer = true">页面结构</el-button>
      <el-button type="info" round @click="getSaveList(1)">页面列表</el-button>
      <el-button type="primary" round @click="saveData">保存数据</el-button>
      <el-button type="success" round @click="downLoadJson">导出页面</el-button>
      <el-button type="danger" round @click="()=>{this.deleteComponent(this.showList)}">删除组件</el-button>
    </div>
    <div class="box" @click="clickDown">
      <!-- 左侧拖拽菜单 -->
      <draggable
        :list="startList"
        class="leftBox"
        :options="options"
        @clone="clone"
        @remove="remove"
      >
        <p v-for="(item,index) in startList" :key="index" class="p">{{item.name}}</p>
      </draggable>
      <!-- 主舞台 -->
      <draggable
        :list="showList"
        class="centerBox"
        :options="{group: { name: 'abc' }}"
        @choose="(e)=>{choose(e,this.showList)}"
      >
        <template v-for="(t,i) in showList">
          <!-- form 容器 -->
          <component
            :key="i"
            :is="t.componentName"
            v-bind="t.data"
            v-if="t.componentName=='hb-form'"
            :data-key="t.key"
          >
            <!--  子容器 -->
            <hbFormItem
              :comData="t.children"
              :childrenOptions="t.childrenOptions"
              :rules="t.rules"
              @chooseElement="chooseElement"
              @changeItem="changeItem"
            />
          </component>

          <!-- grid -->
          <component
            :key="i"
            :is="t.componentName"
            v-bind="t"
            :comData="t"
            v-else-if="t.componentName=='hb-grid'"
            :data-key="t.key"
            @chooseElement="chooseElement"
            @changeItem="changeItem"
          ></component>

          <!-- 非form容器 -->
          <component
            :key="i"
            :is="t.componentName"
            :comData="t.data"
            v-bind="t.data"
            v-else
            :data-key="t.key"
          ></component>
        </template>
      </draggable>
      <!-- 右侧属性栏 -->
      <div class="rightBox">
        <div style="margin-top:20px">
          <h4>可配置属性</h4>
        </div>
        <div
          v-if="!currentData.name"
          style="text-align:center;font-size:13px;color: gray;margin-top:22px;"
        >暂无数据</div>
        <showAtr :currentData="currentData" @setCurrentData="setCurrentData" v-else />
      </div>
    </div>
    <!-- 创建新页面弹窗 -->
    <el-dialog title="页面名称" :visible.sync="dialogVisible2" width="30%" :before-close="handleClose">
      <span>
        当前页名称 :
        <el-input v-model="pageName" placeholder="请输入内容" style="width:60%"></el-input>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible2 = false">取 消</el-button>
        <el-button type="primary" @click="addPage">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 左侧抽屉 -->
    <el-drawer :visible.sync="drawer" direction="ltr" :before-close="handleClose" size="15%">
      <el-tree :data="showList" :props="defaultProps"></el-tree>
    </el-drawer>
    <!-- 保存历史列表弹窗 -->
    <el-dialog
      title="项目已保存列表页"
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose"
    >
      <el-table :data="gridData" style="width: 100%" border>
        <el-table-column property="name" label="页面名称"></el-table-column>
        <el-table-column property="key" label="标识"></el-table-column>
        <el-table-column property="value" label="数据">
          <template slot-scope="scope">
            <span>{{ scope.row.value.length>10?scope.row.value.substring(0,10)+'...':scope.row.value }}</span>
          </template>
        </el-table-column>
        <el-table-column property="createTime" label="创建时间">
          <template slot-scope="scope">
            <span>{{ renderTime(scope.row.createTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column property="updateTime" label="更新时间">
          <template slot-scope="scope">
            <span>{{ renderTime(scope.row.updateTime)}}</span>
          </template>
        </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleClick(scope.row)" type="text">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        layout="prev, pager, next"
        :total="totalCount"
        style="float:right;"
        :page-size="10"
        @current-change="changePage"
      ></el-pagination>
    </el-dialog>
  </div>
</template>

<script>
import draggable from "vuedraggable";
import appMix from "../app.js";
import showAtr from "../basicsComponents/showAtr";
import hbirdOptions from "@hbird/comps/dist/hbirdOptions.json";
export default {
  components: {
    draggable,
    showAtr,
  },
  mixins: [appMix],
  mounted() {
    this.startList = hbirdOptions;
  },
  data() {
    return {
      gridData: [], //已经存在的页面数据
      activeName: "first",
      dialogVisible: false,
      dialogVisible2: false,
      drawer: false,
      pageName: "", //新增页面名
      options: {
        sort: false,
        dragClass: "pDrag",
        chosenClass: "chosenClass",
        group: { name: "abc", pull: "clone", put: false },
      },
      showList: [], //舞台中显示的数据
      startList: [], //初始化数据
      currentData: {}, //选中数据,
      chooseSaveKey: "", //保存数据的key
      defaultProps: {
        children: "children",
        label: "name",
      },
      totalCount: null, //数据列表总页数
      iDelete: document.createElement("i"), //删除icon
    };
  },
  methods: {
    chooseElement(e) {
      this.choose(e, this.showList);
    },
    choose(e, arr) {
      arr &&
        arr.forEach((t) => {
          if (e.item.dataset.key == t.key) {
            this.currentData = t;
          } else {
            this.choose(e, t.children);
          }
        });
      this.showDeleteIcon(e.item);
    },
    remove(e) {
      this.startList[e.oldIndex] = JSON.parse(
        JSON.stringify(this.startList[e.oldIndex])
      );
      let num = 0;
      this.showList.forEach((t, d) => {
        console.log(t.children);
        //判断 如果已经存在 form表单  不允许再次往里面添加form 表单
        t.children &&
          t.children.forEach((w, i) => {
            if (w.children) {
              w.children.forEach((x, y) => {
                if (x.name == "表单容器") {
                  w.children.splice(y, 1);
                  this.$message.error("布局组件不支持嵌套form表单");
                }
              });
            }
            if (w.name == "表单容器" || w.name == "表格") {
              t.children.splice(i, 1);
              this.$message.error("表单组件里不能嵌套表单或者表格");
            }
          });
        if (t.name == "表单容器") num += 1;
        if (num > 1) {
          this.showList.splice(d, 1);
          this.$message.error(
            "页面中只允许放入一个表单,可以在弹窗中加入第二个表单"
          );
        }
      });
    },
    clone(e) {
      this.startList = [];
      this.startList = hbirdOptions;
      let _obj = JSON.parse(JSON.stringify(this.startList[e.oldIndex]));
      //克隆时加上唯一id给组件
      _obj.key = new Date().getTime();
      this.startList[e.oldIndex] = _obj;
    },
  },
};
</script>

<style scoped>
.vintage4 {
  color: gold;
  font-size: 30px;
  letter-spacing: 0;
  text-shadow: 0px 1px 0px #999, 0px 2px 0px #888, 0px 3px 0px #777,
    0px 4px 0px #666, 0px 5px 0px #555, 0px 6px 0px #444, 0px 7px 0px #333,
    0px 8px 7px #001135;
}

.box {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50px;
  left: 0;
  opacity: 0.9;
}
.btnGroup {
  position: absolute;
  right: 0;
}

.p {
  padding: 10px 10px 10px 2px;
  margin: 2px;
  text-align: center;
  background: lightblue;
  border-radius: 20px;
}
.pDrag {
  width: 100%;
  padding: 20px 20px 20px 2px;
  text-align: center;
  background: white;
  border: 3px dashed lightskyblue;
}
.chosenClass {
  width: 100%;
  padding: 20px 20px 20px 2px;
  text-align: center;
  background: lightcyan;
  box-shadow: 0 0 10px rgb(6, 155, 241);
  opacity: 0.5;
}
.box {
  display: flex;
  width: 100%;
}
.leftBox {
  width: 10%;
  /* border-top: 1px solid black;
  border-bottom: 1px solid black;
  border-left: 1px solid black; */
}
.centerBox {
  width: 100%;
  min-height: 100%;
  border: 1px solid black;
  left: "15%";
  /* position: "absolute"; */
}
.rightBox {
  width: 15%;
  background: white;
  right: -2px;
  border: 1px solid black;
  text-align: center;
}
</style>
