<template>
  <div :style="{display:isBlock?'block':'inline-block'}">
    <el-button
      :style="{width:width+'px',height:height+'px'}"
      :type="type"
      :plain=" cssType =='plain' ? true:false"
      :round=" cssType =='round' ? true:false"
      v-bind="comData"
    >{{content}}</el-button>
  </div>
</template>
<script>
export default {
  name: "hb-button",
  props: {
    comData: Object,
    width: {
      type: String,
      default: "100",
    },
    height: {
      type: String,
      default: "50",
    },
    content: {
      type: String,
      default: "按钮",
    },
    isBlock: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      default: "",
    },
    cssType: {
      type: String,
      default: "",
    },
  },
  data() {
    return {};
  },
  methods: {},
};
</script>
<style  scoped>
</style>