function fake_click(obj) {
    var ev = document.createEvent("MouseEvents");
    ev.initMouseEvent(
        "click", true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null
    );
    obj.dispatchEvent(ev);
}
let _formData;     //当前页面form内部组件对应数据
function ReJson(showList) {
    _formData = {}
    let _html = {
        str: ''
    }    //组件 
    let _data = {}      //对应组件数据
    showList.forEach((t) => {
        createElement(t, _html, _data)
    })

    removalArr(_data)

    let newData = JSON.parse(JSON.stringify(_data))
    let _arr = Object.keys(_formData)
    if (_arr.length !== 0) {
        newData.formData = _formData
    }
    let jsonStr = JSON.stringify(newData, '', '\t')   //把数据转成字符串 写入

    //输出模板     ${imp}    ${_componentname}   ${jsonStr}
    let _str =
        `<template>
\t<div>
${_html.str}
\t</div>
</template>
<script>
export default {
\tdata() {
    \treturn ${jsonStr}
\t},
}
</script>`
    console.log(_str)
    return _str
}

function deleteAttr(obj, _data) {
    delete obj.data.rule                //form item的属性 干掉,直接在标签上赋值, rule 内置,不暴露   内涵属性rule 也不暴露
    delete obj.data.required
    delete obj.data.itemName
    for (let x in obj.data) {           //空值 全干掉
        if (obj.data[x] === "" || obj.data[x] === undefined) {
            delete obj.data[x]
        }
    }
    _data['data' + obj.key] = obj.data
}

function createElement(t, _html, _data, isInnerForm) {
    if (t.componentName !== 'hb-grid') {
        if (isInnerForm) {
            _html.str += `\n\t\t<${t.componentName} :comData="${'formData.data' + t.key}" v-bind='${'formData.data' + t.key}' >`
        } else {
            _html.str += `\n\t\t<${t.componentName} :comData="${'data' + t.key}" v-bind='${'data' + t.key}' >`
        }
        if (t.children) {
            createHbForm(t, _html, _data)
        }
        _html.str += `\t</${t.componentName}>\n`
    } else {
        createHbGrid(t, _html, _data)
    }
    deleteAttr(t, _data)

}

function createHbForm(t, _html, _data) {
    t.children.forEach(w => {
        if (w.componentName !== 'hb-grid') {
            let required = w.data.required ? `:required='${w.data.required}'` : ''
            let itemName = w.data.itemName ? w.data.itemName : '组件标题'
            _html.str += `\n\t\t\t<el-form-item label="${itemName}" ${required}>\n\t`
            _html.str += `\t\t\t<${w.componentName} v-bind='${'formData.data' + w.key}' :comData='${'formData.data' + w.key}'></${w.componentName}>\n\t`
            _html.str += `\t\t</el-form-item>\n\t`
            addFormData(w)
        } else {
            createHbGrid(w, _html, _data, true)
        }
        deleteAttr(w, _data)
    })
}

function createHbGrid(t, _html, _data, isInnerForm) {
    let width = 100 / t.children.length
    _html.str += `\n\t\t<el-row :comData="${'data' + t.key}" >`
    t.children.forEach(w => {                                           //遍历列数
        _html.str += `\n\t\t\t<el-col style="width:${width}%">`
        w.children.forEach(x => {
            let itemName = x.data.itemName ? x.data.itemName : '组件标题'
            let required = x.data.required ? `:required='${x.data.required}'` : ''
            if (isInnerForm) {
                _html.str += `\n\t\t\t\t<el-form-item label="${itemName}" ${required}>\n\t`
                createElement(x, _html, _data, isInnerForm)
                _html.str += `\t\t\t</el-form-item>\n\t`
                addFormData(x)
            } else {
                createElement(x, _html, _data)
            }
            deleteAttr(x, _data)
        })
        _html.str += `\n\t\t\t</el-col>\n`
    })
    _html.str += `\t\t</el-row>\n`
}

//form用到的数据
function addFormData(data) {
    _formData['data' + data.key] = data.data
}
//数据去重
function removalArr(_data) {
    for (let x in _data) {
        for (let key in _formData) {
            if (x == key) {
                delete _data[x]
            }
        }
    }
}

//名字  内容
export function download(name, data) {
    let _data = ReJson(data)
    return
    var urlObject = window.URL || window.webkitURL || window;
    var downloadData = new Blob([_data]);
    var save_link = document.createElementNS("http://www.w3.org/1999/xhtml", "a")
    save_link.href = urlObject.createObjectURL(downloadData);
    save_link.download = name;
    fake_click(save_link);
}






