<template>
  <draggable
    :options="{group: { name: 'abc' }}"
    :list="comData"
    class="form"
    @choose="choose"
    @add="add"
  >
    <template v-for="(item,i) in comData ">
      <!-- grid -->
      <component
        :is="item.componentName"
        v-bind="item"
        :comData="item"
        v-if="item.componentName=='hb-grid'"
        :childrenOptions="childrenOptions"
        @chooseElement="choose"
        @changeItem="add"
        :key="i"
        :data-key="item.key"
      ></component>
      <el-form-item
        :label="(item.data.itemName?item.data.itemName:'组件标题')"
        :key="item.key + i"
        :data-key="item.key"
        :required="item.data.required?item.data.required:false"
        v-else
      >
        <component :is="item.componentName" v-bind="item.data" :comData="item.data"></component>
      </el-form-item>
    </template>
  </draggable>
</template>

<script>
import draggable from "vuedraggable";
// import hbGrid from "../grid/hb_grid.vue";
export default {
  name: "hbFormItem",
  props: {
    comData: {
      type: Array,
    },
    childrenOptions: {
      type: Object,
    },
  },
  components: {
    draggable,
    // hbGrid
  },
  methods: {
    choose(e) {
      this.$emit("chooseElement", e);
    },
    add(e, _obj) {
      let obj = _obj ? _obj : this.comData[e.newIndex];
      this.$emit("changeItem", obj, this.childrenOptions);
      this.$forceUpdate();
    },
  },
};
</script>

<style scoped>
.form {
  width: 100%;
  min-height: 100px;
  border: 1px solid black;
  padding-bottom: 100px;
}
</style>