<template >
  <el-input
    ref="hb_input"
    :placeholder="placeholder"
    v-model="comData.content"
    :maxlength="Number(maxLength)"
    :style="{width:width!='100%'?width+'px':'100%'}"
    @blur="checkRule"
    :type="type"
    v-bind="comData"
  ></el-input>
</template>
<script>
export default {
  name: "hb-input",
  props: {
    placeholder: {
      type: String,
      default: "请输入内容"
    },
    type: {
      type: String,
      default: ""
    },
    maxLength: {
      type: String
    },
    width: {
      type: String,
      default: "100%"
    },
    rule: {
      default: ""
    },
    comData: Object
  },
  watch: {
    comData: function(newV) {
      if (newV.content || newV.required) {
        this.checkRule();
      }
    }
  },
  data() {
    return {};
  },
  methods: {
    checkRule() {
      var reg = eval(this.rule + "");
      if (this.comData.required && this.comData.content === "") {
        this.showMessage("不能为空");
        return;
      }
      if (reg && !reg.test(this.comData.content)) {
        this.showMessage("输入格式有问题");
        this.comData.content=''
        return;
      }
    },
    showMessage(str) {
      let a = this.$refs.hb_input.$el;
      this.$message({
        showClose: true,
        message: str,
        type: "error",
        onClose: () => {
          a.style.border = "none";
        }
      });
      a.style.border = "1px solid red";
    }
  }
};
</script> 
