<template>
  <!-- 预览页 -->
  <draggable :list="showList" class="centerBox" :options="{group: { name: 'abc' }}">
    <template v-for="(t,i) in showList">
      <!-- form 容器 -->
      <component
        :key="i"
        :is="t.componentName"
        v-bind="t.data"
        v-if="t.componentName=='hb-form'"
        :data-key="t.key"
      >
        <!--  子容器 -->
        <hbFormItem :comData="t.children" :childrenOptions="t.childrenOptions" :rules="t.rules" />
      </component>
      <!-- 非form容器 -->
      <component
        :key="i"
        :is="t.componentName"
        :comData="t.data"
        v-bind="t.data"
        v-else
        :data-key="t.key"
      ></component>
    </template>
  </draggable>
</template>

<script>
import draggable from "vuedraggable";
// import hbFormItem from "../components/form/hb_form_item";

export default {
  components: {
    draggable,
    // hbFormItem
  },
  data() {
    return {
      showList: [] //舞台中显示的数据
    };
  },
  mounted() {
    console.log(this);
    this.showList = JSON.parse(this.$route.query.showList);
  }
};
</script>