<template>
  <hb-input :placeholder="placeholder" :rule="rule" v-bind="comData" :comData="comData" />
</template>
<script>
export default {
  name: "hb-email",
  props: {
    comData: Object,
    placeholder: {
      //外部定义的可配置属性,这里添加默认值
      type: String,
      default: "请输入邮箱"
    }
  },
  data() {
    return {
      rule: /^([a-zA-Z]|[0-9])(\w|)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/ //私有属性这里添加
    };
  }
};
</script>
