  <template>
  <el-table :data="tablData.body" style="width: 100%">
    <template v-for="(item,i) in tablData.headers">
      <el-table-column :prop="item.prop" :label="item.lable" :key="i"></el-table-column>
    </template>
  </el-table>
</template>

  <script>
export default {
  name: "hb-table",
  props: {
    comData: Object
  },
  data() {
    return {
      tablData: {
        body: [],
        headers: []
      }
    };
  },
  watch: {
    comData(newV, oldV) {
      if (newV.api != oldV.api) {
        this.getTablData(newV.api);
      }
    }
  },
  mounted() {
    if (this.comData.api) {
      this.getTablData(this.comData.api);
    }
  },
  methods: {
    getTablData(_url) {
      this.$axios
        .get(_url)
        .then(res => {
          this.tablData = res.data;
          this.$forceUpdate();
        })
        .catch(err => {
          this.$message.error(JSON.stringify(err));
        });
    }
  }
};
</script>