
import { download } from "./tools.js";
import Api from './api'
let appMix = {
    methods: {
        // 如果是form内 出现组件 添加 childrenOptions 配置属性
        changeItem(comData, childrenOptions) {
            if (comData.componentName != "hb-grid") {
                comData.options = Object.assign(
                    JSON.parse(JSON.stringify(childrenOptions)),
                    comData.options
                );
            } else {
                comData.children.forEach((t, i) => {
                    t.children.forEach((w, d) => {
                        w.options = Object.assign(
                            JSON.parse(JSON.stringify(childrenOptions)),
                            w.options
                        );
                    })
                })
            }
        },
        //右侧属性面板赋值
        setCurrentData(currentData) {
            this.currentData = currentData;
        },
        //下载 页面
        downLoadJson() {
            download("currentPage.vue", this.showList);
        },
        //删除组件
        deleteComponent(arr) {
            arr && arr.forEach((t, i) => {
                if (t.key == this.currentData.key) {
                    arr.splice(i, 1)
                } else {
                    this.deleteComponent(t.children)
                }
            })
        },
        addPage() {
            this.dialogVisible2 = false
            this.saveData()
        },
        newPage() {
            this.chooseSaveKey = ''
            this.showList = []
            this.pageName = ''
            this.dialogVisible2 = true
        },
        changePage(e) {
            this.getSaveList(Number(e))
        },
        saveData() {
            if (this.pageName === '') {
                this.dialogVisible2 = true
            } else {
                this.chooseSaveKey == '' ? this.chooseSaveKey = new Date().getTime() : this.chooseSaveKey
                this.$axios.post(Api.save, { key: this.chooseSaveKey, value: JSON.stringify(this.showList), name: this.pageName }).then(res => {
                    if (res.data.error === 0) {
                        this.$message({
                            message: "保存成功",
                            type: "success"
                        });
                    } else {
                        this.$message({
                            message: "保存失败",
                            type: "error"
                        });
                    }
                }).catch(err => {
                    console.log(err)
                })
            }
        },
        getSaveList(_page) {
            this.$axios.get(Api.list, { params: { page: _page } }).then(res => {
                this.gridData = res.data.data.list
                this.totalCount = res.data.data.totalCount
                this.dialogVisible = true
            }).catch(err => {
                console.log(err)
            })
        },
        handleClick(row) {
            this.chooseSaveKey = row.key
            this.showList = JSON.parse(row.value)
            this.pageName = row.name
            this.dialogVisible = false
        },
        handleClose(done) {
            this.$confirm("确认关闭？")
                .then(_ => {
                    done();
                })
                .catch(_ => { });
        },
        showDeleteIcon(item) {
            let p = true;
            if (item.children.length > 0) {
                for (let i = 0; i < item.children.length; i++) {
                    if (item.children[i].nodeName == "I") {
                        this.iDelete.style.display = "block"
                        p = false;
                        return;
                    }
                }
            }
            if (p) {
                this.iDelete.addEventListener("click", () => {
                    this.deleteComponent(this.showList);
                });
                this.iDelete.className = "el-icon-delete";
                this.iDelete.style.fontSize = '20px'
                this.iDelete.style.color = 'blue'
                this.iDelete.style.display = "block"
                item.prepend(this.iDelete);
            }
        },
        clickDown(e) {
            if (e.target.className == 'centerBox') {
                this.iDelete.style.display = "none";
            }
        },
        navToPage() {
            const { href } = this.$router.resolve({
                name: "Preview",
                query: {
                    showList: JSON.stringify(this.showList)
                }
            });
            window.open(href, '_blank');
        },
        renderTime(date) {
            var dateee = new Date(date).toJSON();
            return new Date(+new Date(dateee) + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
        }

    },
}

export default appMix