<template>
  <el-select v-model="comData.value" :placeholder="placeholder">
    <el-option v-for="item in list" :key="item.value" :label="item.label" :value="item.value"></el-option>
  </el-select>
</template>

<script>
export default {
  name: "hb-select",
  props: {
    comData: Object,
    list: {
      type: Array,
      default: () => {
        return [
          { value: "示例1", label: "示例1" },
          { value: "示例2", label: "示例2" }
        ];
      }
    },
    placeholder: {
      type: String,
      default: "请选择"
    }
  },
  data() {
    return {};
  }
};
</script>