<template>
  <div :style="{display:isBlock?'block':'inline-block'}">
    <p>{{content}}</p>
  </div>
</template>
<script>
export default {
  name: "hb-text",
  props: {
    content: {
      type: String,
      default: "这是一段文字",
    },
    isBlock: {
      type: Boolean,
      default: false,
    },
  },
};
</script>