<template>
  <hb-input :comData="comData" v-bind="comData" :rule="rule" :placeholder="placeholder" />
</template>
<script>
export default {
  name: "hb-phone",
  props: {
    comData: Object,
    placeholder: {
      //外部定义的可配置属性,这里添加默认值
      type: String,
      default: "请输入手机号"
    }
  },
  data() {
    return {
      rule: /^[1][3,4,5,7,8][0-9]{9}$/
    };
  },
  methods: {}
};
</script>
