<template>
  <el-date-picker
    v-model="comData.content"
    :type="type"
    :placeholder="placeholder"
    :range-separator="separator"
    :start-placeholder="start_placeholder"
    :end-placeholder="end_placeholder"
    v-bind="comData"
  ></el-date-picker>
</template>
<script>
export default {
  name: "hb-date-picker",
  props: {
    comData: Object,
    type: {
      type: String,
      default: "date",
    },
    placeholder: {
      type: String,
      default: "请选择日期",
    },
  },
  data() {
    return {
      separator: "至",
      start_placeholder: "开始日期",
      end_placeholder: "结束日期",
    };
  },
  watch: {
    comData(newV) {
      if (newV.type == "daterange" || newV.type == "monthrange") {
        this.separator = "至";
        this.start_placeholder = "开始日期";
        this.end_placeholder = "结束日期";
      } else {
        this.separator = "";
        this.start_placeholder = "";
        this.end_placeholder = "";
      }
    },
  },
};
</script>