<template>
  <el-row>
    <template v-for="(t,i) in comData.children">
      <el-col
        :key="i"
        :style="{width:backWidth,'min-height': '100px', border:'1px solid black',padding:'20px',}"
      >
        <draggable
          :options="{group: { name: 'abc' }}"
          :list="t.children"
          style="width:100%;min-height:50px;"
          @choose="choose"
          @add="add"
        >
          <template v-for="(item,i) in t.children">
            <!-- form 容器 -->
            <component
              :key="i"
              :is="item.componentName"
              v-bind="item.data"
              v-if="item.componentName=='hb-form'"
              :data-key="item.key"
            >
              <!--  子容器 -->
              <hbFormItem
                :comData="item.children"
                :childrenOptions="item.childrenOptions"
                :rules="item.rules"
                @chooseElement="choose"
                @changeItem="changeItem"
              />
            </component>
            <!-- 在表单里面的情况 -->
            <el-form-item
              :label="(item.data.itemName?item.data.itemName:'组件标题')"
              :key="item.key + i"
              :data-key="item.key"
              :required="item.data.required?item.data.required:false"
              v-else-if="item.options.required"
            >
              <component
                :is="item.componentName"
                :key="item.key + i"
                v-bind="item.data"
                :comData="item.data"
              ></component>
            </el-form-item>

            <component
              :is="item.componentName"
              :key="item.key + i"
              v-bind="item.data"
              :comData="item.data"
              :data-key="item.key"
              v-else
            ></component>
          </template>
        </draggable>
      </el-col>
    </template>
  </el-row>
</template>
<script>
import draggable from "vuedraggable";
// import hbFormItem from "../form/hb_form_item.vue";
export default {
  name: "hb-grid",
  props: {
    comData: Object,
    data: Object,
    childrenOptions: Object,
  },
  components: {
    draggable,
    // hbFormItem,
  },
  computed: {
    backWidth() {
      return this.comData.data.columnNum
        ? 100 / Number(this.comData.data.columnNum) + "%"
        : "50%";
    },
  },
  watch: {
    data(newV, oldV) {
      if (newV.columnNum != oldV.columnNum) {
        this.setChildren();
      }
    },
  },
  methods: {
    setChildren() {
      this.comData.children = [];
      for (
        let index = 0;
        index < Number(this.comData.data.columnNum);
        index++
      ) {
        let obj = {
          children: [],
          name: `第${index + 1}列`,
        };
        this.comData.children.push(obj);
      }
    },
    choose(e) {
      this.$emit("chooseElement", e);
    },
    changeItem(_obj, _options) {
      this.$emit("changeItem", _obj, _options);
    },
    add(e) {
      this.$emit("changeItem", e, this.comData);
      this.$forceUpdate();
    },
  },
};
</script>
