<template>
  <hb-input :placeholder="placeholder" :rule="rule" :comData="comData" v-bind="comData" />
</template>
<script>
export default {
  name: "hb-number",
  props: {
    comData: Object,
    placeholder: {
      //外部定义的可配置属性,这里添加默认值
      type: String,
      default: "请输入数值"
    }
  },
  data() {
    return {
      rule: /^[0-9]*$/
    };
  },
  methods: {}
};
</script>
